import { useRouteMatch } from 'react-router-dom';
import Container from '@material-ui/core/Container';

import WorkChart from '../../components/WorkChart';
import NodesInfo from '../../components/NodesInfo';
import History from '../../components/History';
import Errors from '../../components/Errors';

import styles from './machine.module.scss';

function Machine() {
  const route: any = useRouteMatch('/machine/:machineId');
  const machineId: any = route?.params?.machineId;

  return (
    <Container className={styles.container} style={{ display: 'flex' }}>
      <div className={styles.leftBlock}>
        <WorkChart machineId={machineId} />
        <NodesInfo />
      </div>
      <div className={styles.rightBlock}>
        <History />
        <Errors />
      </div>
    </Container>
  );
}

export default Machine;
