import styles from './errors.module.scss';

const errors = [
  {
    code: '1024',
    errorCode: '11111111',
    errorCodeType: 'Код ошибки ПЛК',
    date: new Date(1599992475978),
  },
  {
    code: '2048',
    errorCode: '742F0000',
    errorCodeType: 'Код ошибки движения',
    date: new Date(1599991145678),
  },
];

function Errors() {
  return (
    <div className={styles.container}>
      <div className={styles.title}>История статусов</div>
      <div className={styles.errors}>
        {errors.map((error) => {
          return (
            <div className={styles.error}>
              <div className={styles.row}>
                <div className={styles.text} style={{ maxWidth: 180 }}>
                  {error.code}
                </div>
                <div className={styles.text}>{error.date.toLocaleString()}</div>
              </div>

              <div className={styles.row}>
                <div className={styles.text} style={{ maxWidth: 180 }}>
                  {error.errorCodeType}
                </div>
                <div className={styles.text}>{error.errorCode}</div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default Errors;
