import { useState, useEffect } from 'react';

import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';

import Loading from './components/Loading';
import Login from './pages/Login';
import Account from './pages/Account';
import Machine from './pages/Machine';

import Header from '../src/components/Header';

function App() {
  const [isUserAuth, setIsUserAuth] = useState<boolean | null>(null);

  useEffect(() => {
    setTimeout(() => {
      setIsUserAuth(true);
    }, 500);
  }, []);

  if (isUserAuth === null) {
    return <Loading />;
  }

  const registration = () => {
    setIsUserAuth(true);
  };

  const login = () => {
    setIsUserAuth(true);
  };

  const logout = () => {
    setIsUserAuth(false);
  };

  return (
    <BrowserRouter>
      {!isUserAuth ? (
        <Switch>
          <Route exact path='/login'>
            <Login registration={registration} login={login} />
          </Route>

          <Route path=''>
            <Redirect to='/login' />
          </Route>
        </Switch>
      ) : (
        <Switch>
          <Route exact path='/account'>
            <>
              <Header logout={logout} />
              <Account />
            </>
          </Route>

          <Route exact path='/machine/:machineId'>
            <>
              <Header logout={logout} />
              <Machine />
            </>
          </Route>

          <Route path=''>
            <Redirect to='/account' />
          </Route>
        </Switch>
      )}
    </BrowserRouter>
  );
}

export default App;
