import styles from './history.module.scss';

const history = [
  {
    text: 'Выключение',
    date: new Date(1599992475978),
  },
  {
    text: 'Включение',
    date: new Date(1599992365878),
  },
  {
    text: 'Запуск работы',
    date: new Date(1599992355778),
  },
  {
    text: 'Превышена нагрузка на ось загиба',
    date: new Date(1599991145678),
  },
];

function History() {
  return (
    <div className={styles.container}>
      <div className={styles.title}>История статусов</div>
      <div className={styles.history}>
        {history.map((log) => {
          return (
            <div className={styles.log}>
              <div className={styles.text} style={{ maxWidth: 180 }}>
                {log.text}
              </div>
              <div className={styles.text}>{log.date.toLocaleString()}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default History;
