import { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import styles from './account.module.scss';

function Account() {
  const [machines, setMachines] = useState<Array<any>>([]);
  const [error, setError] = useState('');
  const formRef = useRef(null);

  useEffect(() => {
    setMachines([
      { id: 1, name: 'machine1' },
      { id: 2, name: 'machine2' },
    ]);
  }, []);

  const addNewMachine = () => {
    setError('Неверное имя или пароль');
  };

  return (
    <Container className={styles.container} style={{ display: 'flex' }}>
      <div className={styles.machinesList}>
        <div className={styles.listTitle}>Список машин:</div>
        {machines.map((machine) => (
          <div key={machine.id} className={styles.machine}>
            <Link className={styles.link} to={`/machine/${machine.id}`}>
              {machine.name}
            </Link>
          </div>
        ))}
      </div>
      <div className={styles.formWrap}>
        <form ref={formRef} className={styles.form}>
          <TextField
            name='name'
            variant='outlined'
            label='Id'
            style={{ marginBottom: 8 }}
            size='small'
            error={Boolean(error)}
          />
          <TextField
            name='pass'
            variant='outlined'
            label='Пароль'
            style={{ marginBottom: 18 }}
            type='password'
            size='small'
            error={Boolean(error)}
          />
          <Button variant='contained' onClick={addNewMachine}>
            Добавить машину
          </Button>
        </form>
        {error ? <div className={styles.error}>{error}</div> : null}
      </div>
    </Container>
  );
}

export default Account;
