import LinearProgress from '@material-ui/core/LinearProgress';

import styles from './nodesinfo.module.scss';

const nodes = [
  {
    name: 'Нож',
    resource: 5000,
    resourceMax: 8000,
    resourceDescription: 'Отработано циклов',
    temperature: 20,
    warningMessage: 'Замена через 21 день',
    dangerMessage: null,
  },
  {
    name: 'Лента',
    resource: 170,
    resourceMax: 180,
    resourceDescription: 'Отработано циклов',
    temperature: null,
    warningMessage: null,
    dangerMessage: 'Замена через 2 дня',
  },
  {
    name: 'Нож',
    resource: 5000,
    resourceMax: 8000,
    resourceDescription: 'Отработано циклов',
    temperature: 20,
    warningMessage: 'Замена через 21 день',
    dangerMessage: null,
  },
];

function NodesInfo() {
  return (
    <div className={styles.container}>
      <div className={styles.title}>Узлы</div>
      <div className={styles.nodesList}>
        {nodes.map((node) => {
          return (
            <div className={styles.node}>
              <div className={styles.nodeTitle}>{node.name}</div>

              {node.resource && node.resourceMax && node.resourceDescription ? (
                <>
                  <div className={styles.text}>{node.resourceDescription}</div>
                  <LinearProgress
                    variant='determinate'
                    value={(node.resource / node.resourceMax) * 100}
                    style={{ width: 200, height: 4, marginBottom: 6 }}
                  />
                  <div className={styles.row}>
                    <div className={styles.text}>
                      {node.resource}/{node.resourceMax}
                    </div>
                    <div className={styles.text}>
                      {Math.round((node.resource / node.resourceMax) * 100)} %
                    </div>
                  </div>
                </>
              ) : null}

              {node.temperature !== null ? (
                <div className={styles.text}>{node.temperature} °C</div>
              ) : null}

              {node.warningMessage ? (
                <div className={[styles.text, styles.warning, styles.message].join(' ')}>
                  {node.warningMessage}
                </div>
              ) : null}

              {node.dangerMessage ? (
                <div className={[styles.text, styles.danger, styles.message].join(' ')}>
                  {node.dangerMessage}
                </div>
              ) : null}
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default NodesInfo;
