import { useRef, useState, useEffect } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import styles from './login.module.scss';

interface props {
  registration(): void;
  login(): void;
}

function Login({ registration, login }: props) {
  const [loginMod, setLoginMod] = useState(true);
  const [error, setError] = useState('');
  const formRef = useRef(null);

  useEffect(() => {
    setError('');
  }, [loginMod]);

  const sendLogin = () => {
    login();
  };

  const sendRegistration = () => {
    setError('Такой пользователь уже существует');
  };

  return (
    <div className={styles.container}>
      {loginMod ? (
        <>
          <div className={styles.title}>Вход</div>
          <form ref={formRef} className={styles.form}>
            <TextField
              name='login'
              style={{ marginBottom: 10 }}
              label='Логин'
              error={Boolean(error)}
            />
            <TextField
              name='pass'
              style={{ marginBottom: 10 }}
              label='Пароль'
              type='password'
              error={Boolean(error)}
            />
            <div style={{ height: 58, width: '100%' }}></div>
          </form>
          <Button
            style={{ fontSize: 18, margin: '20px 0' }}
            variant='contained'
            onClick={sendLogin}
          >
            Отправить
          </Button>
          <div>
            <Button style={{ fontSize: 12 }} onClick={() => setLoginMod(false)}>
              Зарегистрироватся
            </Button>
          </div>
          {error ? <div className={styles.error}>{error}</div> : null}
        </>
      ) : (
        <>
          <div className={styles.title}>Регистрация</div>
          <form ref={formRef} className={styles.form}>
            <TextField
              name='login'
              style={{ marginBottom: 10 }}
              label='Логин'
              error={Boolean(error)}
            />
            <TextField
              name='pass'
              style={{ marginBottom: 10 }}
              label='Пароль'
              type='password'
              error={Boolean(error)}
            />
            <TextField
              name='repeatPass'
              style={{ marginBottom: 10 }}
              label='Повторите пароль'
              type='password'
              error={Boolean(error)}
            />
          </form>
          <Button
            style={{ fontSize: 18, margin: '20px 0' }}
            variant='contained'
            onClick={sendRegistration}
          >
            Отправить
          </Button>
          <div>
            <Button style={{ fontSize: 12 }} onClick={() => setLoginMod(true)}>
              Войти
            </Button>
          </div>
          {error ? <div className={styles.error}>{error}</div> : null}
        </>
      )}
    </div>
  );
}

export default Login;
